<template>
  <el-dialog
    width="90%"
    :visible.sync="visible"
    :before-close="() => $emit('close')"
  >
    <el-row slot="title" class="dialog-header">
      {{gid ? '' : ''}}
    </el-row>
    <el-form
      id="analysis-brand-group-form"
      :model="analysisBrandGroupForm"
      :rules="rules"
      ref="analysisBrandGroupForm"
      label-position="top"
    >
      <el-form-item v-if="gid" label="ID">
        <el-label :width="150">
          {{gid}}
        </el-label>
      </el-form-item>
      <el-form-item
        label=""
        prop="label"
        style="width: 400px;"
      >
        <el-input
          placeholder=""
          v-model="label"
          clearable
        />
      </el-form-item>
      <el-form-item label="">
        <el-transfer
          filterable
          v-model="selectedBrands"
          :data="allBrands"
          :titles="['', '']"
        />
      </el-form-item>
    </el-form>
    <el-row slot="footer" class="dialog-footer">
      <el-button
        size="small"
        @click="() => $emit('close')"
      >
      </el-button>
      <el-button
        v-if="gid"
        type="primary"
        size="small"
        @click="update"
      >
      </el-button>
      <el-button
        v-else
        type="primary"
        size="small"
        @click="create"
      >
      </el-button>
    </el-row>
  </el-dialog>
</template>

<script>
  import { mapFields } from 'vuex-map-fields'
  import ElLabel from '../../common/ElLabel'

  export default {

    components: {
      ElLabel
    },

    props: {
      visible: Boolean
    },

    data() {
      return {
        rules: {
          label: [
            {
              required: true,
              message: '',
              trigger: 'blur'
            }
          ],
        }
      }
    },

    methods: {
      create() {
        this.$refs.analysisBrandGroupForm.validate(async valid => {
          if (valid) {
            await this.$http.analysisBrandGroup.$create(this.analysisBrandGroupForm).then(() => {
              this.$notify({
                type: 'info',
                message: '',
                position: 'bottom-right'
              })
              this.$emit('close')
            })
          }
        })
      },

      async update() {
        this.$refs.analysisBrandGroupForm.validate(async valid => {
          if (valid) {
            await this.$http.analysisBrandGroup.$update(this.analysisBrandGroupForm).then(() => {
              this.$notify({
                type: 'info',
                message: '',
                position: 'bottom-right'
              })
              this.$emit('close')
            })
          }
        })
      },
    },

    computed: {
      ...mapFields({
        analysisBrandGroupForm: 'analysisBrandGroupForm',
        gid: 'analysisBrandGroupForm.gid',
        label: 'analysisBrandGroupForm.label',
        selectedBrands: 'analysisBrandGroupForm.brands',
        brands: 'brands'
      }),

      allBrands: function() {
        return this.brands.map(b => ({key: b.code, label: `${b.code}-${b.name}-${b.market}`}))
      }
    }
  }
</script>

<style scoped>
  .el-transfer >>> .el-transfer-panel {
    width: 40% !important;
  }

  #analysis-brand-group-form {
    padding-left: 10px;
    padding-bottom: 10px;
  }
</style>
